.mat-progress-spinner circle, .mat-spinner circle {
    stroke: #1e95a5;
    }
 .upload-spinner circle{
     stroke:red !important;
 }
.finish-spinner circle{
    stroke:green !important;
}
.mat-progress-bar-fill::after {
        background-color: #1e95a5;
      }
